<template>
  <el-menu
    :default-active="activeIndex"
    class="el-menu-demo nav-el-menu"
    :mode="mode"
    :router="true"
  >
    <el-menu-item
      v-for="(item, index) in items"
      :key="item.id"
      :index="item.id"
      :route="item.route"
      ><i :class="item.iconClass"></i>{{ item.text }}</el-menu-item
    >
  </el-menu>
</template>

<script>
export default {
  data() {
    return {
      activeIndex: "1",
      mode: "horizontal",
      routepath: "/",
      items: [
        {
          id: "1",
          route: { path: "/" },
          iconClass: "el-icon-edit",
          text: "首页",
        },
        {
          id: "2",
          route: { path: "/archives" },
          iconClass: "el-icon-edit",
          text: "归档",
        },
        {
          id: "3",
          // route: { name: 'Main' },
          iconClass: "el-icon-edit",
          text: "首页",
        },
        {
          id: "4",
          // route: { name: 'Main' },
          iconClass: "el-icon-edit",
          text: "首页",
        },
      ],
    };
  },
  watch: {
    $route(to, from) {
      this.changeActiveIndex();
    }
  },
  methods: {
    changeActiveIndex() {
      if(this.$route.path){

        this.routepath = this.$route.path;
        let it = this.items.find((item) => {
          if(item.route)
            return item.route.path === this.routepath;
          else
            return false;
        });
        if(it)
          this.activeIndex = it.id;
        else
          this.activeIndex = '1';
      }
    },
  },
  beforeMount() {
    this.changeActiveIndex()
  },
  mounted() {},
};
</script>

<style lang="scss" scoped>
.nav-el-menu {
  border-bottom: none !important;
}
</style>