// 拉取该文件夹下所有文件信息
const filesMD = require.context('/public/reso', true, /\.md$/);
const filesMDNames = filesMD.keys();
console.log(filesMDNames);
const tmepListDatas = [];

filesMDNames.map((item) => {
  const listObj = {};
  const listItemS = item.split('/');
  if (listItemS.length > 0) {
    let listItemCopy = [...listItemS];
    listItemCopy[listItemCopy.length-1] = listItemCopy[listItemCopy.length-1].replace('.md', '');
    listItemCopy.shift();
    listObj.title = listItemCopy.join('-');
    listObj.path = item.replace('.\/','\/reso\/');
  }
  const imgSrc =
    "https://cdn2.hubspot.net/hubfs/322787/Mychefcom/images/BLOG/Header-Blog/photo-culinaire-pexels.jpg"
    listObj.imgSrc = imgSrc;
  return tmepListDatas.push(listObj);
});

export default {
  'get|/article': option => {
    return {
      status: 200,
      message: 'success',
      data: tmepListDatas
    };
  }
}